@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Noto Sans JP', sans-serif}body{background:#262626}.container{background:#2A9D8F;border-radius:0 0 10px 10px;margin:0 auto;max-width:1000px;width:90%}

h1{background:#264653;color:white;padding:10px 5px;text-align:center}main{display:flex;justify-content:space-between;padding:20px 40px}.form-component{width:49%}.form-component .warning{background:#fcd5ce;color:#ff0000;font-size:12px;margin-bottom:10px;padding:3px;text-align:center}.form-component form{color:white;margin-bottom:20px}.form-component form p{margin-bottom:5px}.form-component form .input{background:#2A9D8F;border:none;border-bottom:1px solid white;color:white;margin-bottom:15px;padding:7px 10px;width:100%;-moz-appearance:none;text-indent:0.01px;text-overflow:''}.form-component form button{background:#E9C46A;border:none;border-radius:5px;cursor:pointer;padding:5px;width:100%}.form-component form button:hover{background:#F2DCA6}.clima-container{background:white;padding:35px 20px;width:49%}.clima-container p{margin-bottom:15px}.clima-container .temperatura{text-align:center;font-size:40px}.spinner{margin:100px auto 0;width:70px;text-align:center}.spinner>div{width:18px;height:18px;background-color:#333;border-radius:100%;display:inline-block;-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both}.error{text-align:center;width:100%}.spinner .bounce1{-webkit-animation-delay:-0.32s;animation-delay:-0.32s}.spinner .bounce2{-webkit-animation-delay:-0.16s;animation-delay:-0.16s}@-webkit-keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,100%{transform:scale(0)}40%{transform:scale(1)}}

